import React, { Component } from "react";
import ParticlesBg from "particles-bg";
import Fade from "react-reveal";

class Header extends Component {
  render() {
    if (!this.props.data) return null;

    const project = this.props.data.project;
    const github = this.props.data.github;
    const name = this.props.data.name;
    const description = this.props.data.description;
    const backgroundImage = "images/" +  this.props.data.backgroundImage; // 确保数据包含背景图片的 URL
    return (
      <header id="home">
        {/* <ParticlesBg type="circle" bg={true} /> */}
        <div style={{
          backgroundImage: `url(${backgroundImage})`,
          // backgroundColor: 'red',
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          zIndex: -1
        }}></div>
        <nav id="nav-wrap">
          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">
            Show navigation
          </a>
          <a className="mobile-btn" href="#home" title="Hide navigation">
            Hide navigation
          </a>

          <ul id="nav" className="nav">
            <li className="current">
              <a className="smoothscroll" href="#home" style= {{fontSize: "30px"}}>
                引言
              </a>
            </li>

            {/* <li>
              <a className="smoothscroll" href="#about">
                About
              </a>
            </li> */}

            <li>
              <a className="smoothscroll" href="#resume" style={{ fontSize: "30px" ,color:"black"}}>
                新闻
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#portfolio" style={{ fontSize: "30px", color: "black" }}>
                图片
              </a>
            </li>

            {/* <li>
              <a className="smoothscroll" href="#contact">
                Contact
              </a>
            </li> */}
          </ul>
        </nav>

        <div className="row banner">
          <div className="banner-text">
            <Fade bottom>
              <h1 className="responsive-headline" style={{ color: "black" }}>{name}</h1>
            </Fade>
            <Fade bottom duration={1200}>
              <h3 style={{ color: "black" ,marginTop: "200px" }}>{description}.</h3>
            </Fade>
            <hr />
            {/* <Fade bottom duration={2000}>
              <ul className="social">
                <a href={project} className="button btn project-btn">
                  <i className="fa fa-book"></i>Project
                </a>
                <a href={github} className="button btn github-btn">
                  <i className="fa fa-github"></i>Github
                </a>
              </ul>
            </Fade> */}
          </div>
        </div>

        <p className="scrolldown">
          <a className="smoothscroll" href="#about">
            <i className="icon-down-circle"></i>
          </a>
        </p>
      </header>
    );
  }
}

export default Header;
